<template>
<div class="search">
  <input class="search-input" type="text" placeholder="请输入城市名或拼音"
  v-model="keyword"
  >
  <div class="search-content"
       ref="search"
       v-show="keyword"
  >
    <ul>
      <li class="search-item"
          v-for="item in list"
          :key="item.id"
          @click="handleCityClick(item.name)">
        {{item.name}}
      </li>
      <li class="search-item border-bottom"
          v-show="hasNoData">
        没有找到匹配数据
      </li>
    </ul>
  </div>
</div>
</template>

<script>
/* eslint-disable */
import Bscroll from 'better-scroll'
export default {
    name: "CitySearch",
    props: {
      cities: Object
    },
    data () {
      return {
        keyword: '',
        list: [],
        timer: null
      }
    },
    mounted () {
      this.scroll = new Bscroll(this.$refs.search);
    },
    computed: {
      hasNoData () {
        return !this.list.length
      }
    },
    methods: {
      handleCityClick (city){
        // alert(city)
        // this.$store.dispatch('changeCity',city)
        this.$store.commit('changeCity',city)
        /*跳转到首页*/
        this.$router.push('/')
      }
    },
    watch: {
      keyword () {
        if (this.timer) {
          clearTimeout(this.timer)
        }
        if (!this.keyword){
          this.list = []
          return
        }
        this.timer = setTimeout(()=>{
          const  result = []
          for(let i in this.cities){
            this.cities[i].forEach((value)=>{
              if(value.spell.indexOf(this.keyword)> -1 ||
                  value.name.indexOf(this.keyword) > -1
                ){
                result.push(value)
              }
              this.list = result
            },100)
          }
        })
      }
    }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl';
  .search
    height :.72rem
    background :$bgColor
    padding :0.1rem
    .search-input
      box-sizing :border-box
      height :.62rem
      line-height :.62rem
      width:100%
      text-align :center
      border-border-radius : .1rem
      color :#666
  .search-content
    z-index :1
    overflow :hidden
    position :absolute
    top:1.58rem
    left :0
    right :0
    bottom :0
    background :#eee
    .search-item
      line-height :.62rem
      padding-left :.2rem
      background :#fff
      cursor:pointer


</style>



